                  required
                ></b-form-input>
              </b-form-group>
              <p>ex)Í∞êÏÇ¨Î•º ÏúÑÌïú Í∏∞ÎèÑÎ¨∏ÏùÑ ÏûëÏÑ±Ìï¥ Ï£ºÏÑ∏Ïöî.</p>
              <b-button type="submit" variant="primary" class="mt-2 w-100">Í∏∞ÎèÑÎ¨∏ ÏûëÏÑ±</b-button>
            </b-form>

